/* noto-sans-kr-100 - korean */
@font-face {
  font-family: 'Noto Sans KR';
  font-style: normal;
  font-weight: 100;
  src: url('../fonts/NotoSans/noto-sans-kr-v8-korean-100.eot'); /* IE9 Compat Modes */
  src: local('Noto Sans KR Thin'), local('NotoSansKR-Thin'),
  url('../fonts/NotoSans/noto-sans-kr-v8-korean-100.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
  url('../fonts/NotoSans/noto-sans-kr-v8-korean-100.woff2') format('woff2'), /* Super Modern Browsers */
  url('../fonts/NotoSans/noto-sans-kr-v8-korean-100.woff') format('woff'), /* Modern Browsers */
  url('../fonts/NotoSans/noto-sans-kr-v8-korean-100.ttf') format('truetype'), /* Safari, Android, iOS */
  url('../fonts/NotoSans/noto-sans-kr-v8-korean-100.svg#NotoSansKR') format('svg'); /* Legacy iOS */
}
