::-webkit-scrollbar {width: 8px; height: 8px; border: 3px solid #fff; }
::-webkit-scrollbar-button:start:decrement,::-webkit-scrollbar-button:end:increment {display: block; height: 10px; background: #efefef}
::-webkit-scrollbar-track {background: #efefef; -webkit-border-radius: 10px; border-radius:10px;}
::-webkit-scrollbar-thumb {height: 50px; width: 50px; background: rgba(0,0,0,.2); -webkit-border-radius: 8px; border-radius: 8px;}

header {
    position: fixed;
    top: 0;
    left: 0;
    padding: 4.7vw;
    z-index: 999;
    h1 {
        @include fontSize(1.4vw,700);
        font-family: 'Rock Salt', cursive;
    }
}

.view-folder {
    position: fixed;
    left: 3vw;
    top: 0;
    width: 88vw;
    height: 100vh;
    z-index: 1;
    padding-left: 17.6vw;
    overflow-y: scroll;
    >button {
        position: fixed;
        right: 50px;
        bottom: -100px;
        border: 0;
        background: none;
        width: 70px;
        height: 70px;
        opacity: 0;
        &.showing {
            opacity: 1;
            bottom: 50px;
            transition: .5s;
        }
        img {
            width: 100%;
        }
    }
    .bg-content {
        width: calc( 100% - 17.6vw );
        opacity: 0;
        position: absolute;
        top: 0;
        right: 0;
        &.hide {
            display: none;
        }
        &.showing {
            opacity: 1;
        }
        img {
            width: 100%;
            margin-bottom: 40px;
            cursor: pointer;
        }
    }
}

.content-body {
    padding: 0 9vw 0 3vw;
    background: #F5F5F5;
    width: 100%;
    height: 100vh;
    .copyright {
        position: fixed;
        right: -100px;
        transform: rotate(-90deg) translate(0,-50%);
        font-family: 'Rock Salt', cursive;
        top: 50%;
        @include fontSize(20px,700);
        opacity: .8;
    }
    .contents {
        &:after {
            @include clearBoth;
        }
        .bar-item {
            position: fixed;
            z-index: 2;
            background: #F5F5F5;
            cursor: pointer;
            float: left;
            border-left: 1px solid #ECECEC;
            width: calc( 88vw/5 );
            height: 100vh;
            padding-top: 32vh;
            padding: 32vh 1.7vw 0;
            &:hover {
                strong {
                    color: #2E2E2E;
                    transition: 1s;
                }
            }
            &:first-child {
                left: 3vw;
                &.left-position {
                    position: fixed;
                    left: -17.6vw;
                    transition: .5s;
                }
                &.side-position {
                    position: fixed;
                    left: 3vw;
                    transition: .5s;
                }
            }
            &:nth-child(2) {
                left: 20.6vw;
                &.left-position {
                    position: fixed;
                    left: -17.6vw;
                    transition: .5s;
                }
                &.side-position {
                    position: fixed;
                    left: 3vw;
                    transition: .5s;
                }
            }
            &:nth-child(3) {
                left: 38.2vw;
                &.left-position {
                    position: fixed;
                    left: -17.6vw;
                    transition: .5s;
                }
                &.side-position {
                    position: fixed;
                    left: 3vw;
                    transition: .5s;
                }
            }
            &:nth-child(4) {
                left: 55.8vw;
                &.left-position {
                    position: fixed;
                    left: -17.6vw;
                    transition: .5s;
                }
                &.side-position {
                    position: fixed;
                    left: 3vw;
                    transition: .5s;
                }
            }
            &:nth-child(5) {
                left: 73.4vw;
                &.left-position {
                    position: fixed;
                    left: -17.6vw;
                    transition: .5s;
                }
                &.side-position {
                    position: fixed;
                    left: 3vw;
                    transition: .5s;
                }
            }
            strong {
                color: #CA2F19;
                display: block;
                @include fontSize(4vw,700);
                margin-bottom: 10vh;
                font-family: 'Rock Salt', cursive;
            }
            .text-box {
                margin-bottom: 40px;
                p {
                    @include fontSize(1.1vw,700);
                    margin-bottom: 5px;
                }
                small {
                    @include fontSize(.9vw,300);
                }
                span {
                    display: block;
                    margin-top: 20px;
                    @include fontSize(1vw,500);
                }
            }
            .img-box {
                margin-bottom: 50px;
                img {
                    width: 100%;
                }
            }
            >p {
                @include fontSize(15px,500);
            }
        }
    }
}